.profile-link {
    text-decoration: none;
    color: #13BF49;
}

.profile-link:hover {
    text-decoration: underline;
}

.menu-items {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    padding: 15px 25px;
}

.menu-items .menu {
    text-align: center;
    text-decoration: none;
    /*color: #2B2A29;*/
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    width: 100px;
    height: 100px;
    margin: 0 15px 15px 0;
    border-radius: 4px;
    -webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.09);
            box-shadow: 0 3px 6px rgba(0, 0, 0, 0.09);
    -webkit-transition: all .2s ease-in;
    -o-transition: all .2s ease-in;
    transition: all .2s ease-in;
}

.menu-items .menu:hover {
    -webkit-box-shadow: 0 3px 18px rgba(0, 0, 0, 0.3);
            box-shadow: 0 3px 18px rgba(0, 0, 0, 0.3);
}


